<template>
  <div>
    <b-form @submit.prevent="submit" class="padding-level-1">
      <b-row>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ACCOUNT") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="account"
            class="form-control"
            disabled
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ITEMCODE") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="itemCode"
            class="form-control"
            value="111"
            disabled
          ></b-form-input>
        </b-col>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ITEMNAME") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="itemName"
            class="form-control"
            value="ガス代"
            disabled
          ></b-form-input>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="3">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="6">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{
                $t("SETTINGYEARMONTH")
              }}</label>
            </b-col>
            <b-col cols="12" xl="5">
              <b-form-input
                v-model="$v.yearMonth.$model"
                maxlength="6"
                onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                class="form-control"
                value="202010"
              ></b-form-input>
              <show-errors
                :control="$v.yearMonth"
                :field="$t('SETTINGYEARMONTH')"
              />
              <label v-if="mntFlg" class="text-danger">{{
                $t("MONTHVALIDATION")
              }}</label>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="6">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="3">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{ $t("INDUSTRY") }}</label>
            </b-col>
            <b-col cols="7" xl="4">
              <b-form-input
                v-model="$v.industryName.$model"
                disabled
                class="form-control"
                value="小売業"
              ></b-form-input>
              <show-errors :control="$v.industryName" :field="$t('INDUSTRY')" />
            </b-col>
            <b-col cols="5" xl="3">
              <b-button @click="selectIndustry" variant="primary">{{
                $t("SELECTINDUSTRY")
              }}</b-button>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="3">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="6">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{
                $t("COMPANYSIZE")
              }}</label>
            </b-col>
            <b-col cols="12" xl="6">
              <b-form-select
                v-model="selectedCompanySize"
                :plain="true"
                :options="CompanySizeoptions"
                value-field="item"
                text-field="name"
              ></b-form-select>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <div class="border border-dark pl-4 pr-4 mt-2">
        <label class="title-fs-2">
          {{ $t("COSTFEELINGEVALUATIONJUDGMENTCRITERIA") }}
        </label>
        <br />
        <b-row class="mb-2">
          <b-col xl="6">
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="6">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny"
                  >{{ $t("ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("COSTFEELINGUPPERLIMIT")
                }}</label>
              </b-col>
              <b-col cols="12" xl="5">
                <b-row class="d-flex align-items-center">
                  <b-col cols="10" xl="8">
                    <b-form-input
                      v-model="upperLimit"
                      maxlength="9"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                      class="form-control"
                      value="50"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="2" xl="1">
                    {{ $t("DASHBOARD.COST_UNIT") }}
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="6">
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="6">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny"
                  >{{ $t("ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("COSTFEELINGLOWERLIMIT")
                }}</label>
              </b-col>
              <b-col cols="12" xl="5">
                <b-row class="d-flex align-items-center">
                  <b-col cols="10" xl="8">
                    <b-form-input
                      v-model="lowerLimit"
                      maxlength="9"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                      class="form-control"
                      value="10"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="2" xl="1">
                    {{ $t("DASHBOARD.COST_UNIT") }}
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </div>
      <br /><br />
      <b-button
        type="submit"
        variant="primary"
        class="px-4 align-content-center"
        >{{ buttonSelect }}
        <i v-if="isBusy" class="fa fa-spinner fa-spin" />
      </b-button>
      <br /><br />
    </b-form>
    <b-modal
      id="tree-modal1"
      ref="tree-modal1"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{}">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("INDUSTRYSELECTION") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mr-3 mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              :treeTitle="''"
              @update-expense-type="updateIndustryItem"
              :industry="true"
              :data="treeData"
              :tree-options="treeOptions"
            />
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            :disabled="currentSelectedIndustryItem == ''"
            @click="getIndustryName"
            class="float-right"
            variant="primary"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import SubMenuTree from "@/components/SubMenuTree";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      account: "",
      itemCode: "",
      itemName: "",
      yearMonth: "",
      industryName: "",
      upperLimit: "",
      lowerLimit: "",
      yesrOptions: ["2020", "2021"],
      CompanySizeoptions: [
        { item: "1", name: "小規模" },
        { item: "2", name: "中規模" },
        { item: "3", name: "大規模" },
      ],
      selectedCompanySize: "",
      treeOptions: {},
      allTreeData: [],
      treeData: [],
      allIndustryTreeData: [],
      industryNo: "",
      kbn2Data: [],
      isBusy: "",
      accountCd: "",
      buttonSelect: this.$t("SAVE"),
      mntFlg: false,
      currentSelectedIndustryItem : "",
      mnt: "",
    };
  },
  validations: {
    industryName: { required },
    yearMonth: { required },
  },
  computed: {
    ...mapGetters({
      masterData: "getExpenseYMData",
      selected_industry_item: "getSelectedIndustryItem",
    }),
  },
  watch: {
    yearMonth(value) {
      this.mnt = value.slice(4, 6);
      this.checkMonth(value);
    },
  },
  mounted() {
    this.getIndustryTreeData();
  },

  methods: {
    /**
     * @description function to validate the in between 1 to 12 from (YYYYMM) format
     * @name checkMonth
     */    
    checkMonth(value) {
      this.mnt = value.slice(4, 6);
      if ((value.length > 0 && value.length <= 4) || parseInt(this.mnt) <= 0 || parseInt(this.mnt) > 12) {
        this.mntFlg = true;
      } else {
        this.mntFlg = false;
      }
    },
    /**
     * @description function hide to pop-up modal
     * @name cancel
     */    
    cancel() {
      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to convert number to currency format
     * @name filterByCurrency
     */    
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to convert currency to number
     * @name showProposalModal
     */    
    convertCurrency(value) {
      try {
        return String(value).replace(/,/g, "");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch the store data and map the data to corresponding fields
     * @name getStoreData
     */    
    getStoreData() {
      this.account = this.masterData.AccountCode;
      this.itemCode = this.masterData.ExpenseCode;
      this.itemName = this.masterData.ExpenseName;
      this.accountCd = this.masterData.AccountCd;
      console.log("masterData", this.masterData);
      if (this.masterData.MonthData != null) {
        this.yearMonth = this.masterData.MonthData.item.yearMonth;
        this.selectedCompanySize = this.masterData.MonthData.item.company_size;
        this.upperLimit = this.masterData.MonthData.item.sense_of_cost_up;
        this.lowerLimit = this.masterData.MonthData.item.sense_of_cost_low;
        this.allIndustryTreeData.forEach((item1) => {
          if (item1.industry_kbn == 2) {
            this.kbn2Data.push(item1);
          }
        });
        let industryItem = this._.find(this.kbn2Data, {
          industry_nm: this.masterData.MonthData.item.industry,
        });
        this.industryNo = industryItem.industry_no;
        this.industryName = this.masterData.MonthData.item.industry;
        this.buttonSelect = this.$t("UPDATE");
      } else {
        this.selectedCompanySize = 1;
        this.buttonSelect = this.$t("SAVE");
      }
    },
    /**
     * @description function to show the industry popup-modal
     * @name selectIndustry
     */        
    selectIndustry() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to fetch the industryTreeData form m_industry tabel
     * @name getIndustryTreeData
     */        
    async getIndustryTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allIndustryTreeData = responseData;
        let mapData = [];
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let ParentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              ParentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + ParentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });
        this.$store.dispatch("updateIndustryTree", mapData);
        this.getStoreData();
      }
    },
    /**
     * @description function to update the selected industry(form industry popup-modal) name and no to the store
     * @name updateIndustryItem
     */        
    updateIndustryItem(item) {
      this.allIndustryTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          this.kbn2Data.push(item1);
        }
      });
      let treeItem = this._.find(this.kbn2Data, { industry_nm: item });
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
      this.currentSelectedIndustryItem = treeItem.industry_nm;
    },
    /**
     * @description function to fetch the industy name and no from store and closing the popup-modal
     * @name getIndustryName
     */        
    getIndustryName() {
      this.currentSelectedIndustryItem = this.selected_industry_item.industry_nm;
      this.industryName = this.selected_industry_item.industry_nm;
      this.industryNo = this.selected_industry_item.industry_no;

      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to validate the form data on submiting checking either to create or update
     * @name submit
     */        
    submit() {
      this.$v.yearMonth.$touch();
      let month = this.yearMonth.slice(4, 6);
      if (parseInt(month) > 0) {
        console.log(month);
      }
      this.$v.industryName.$touch();
      if (!this.$v.yearMonth.$invalid && !this.$v.industryName.$invalid) {
        if (parseInt(month) > 0 && parseInt(month) < 13) {
          if (this.masterData.MonthData != null) {
            this.updateData();
          } else {
            this.createData();
          }
        }
      }
    },
    /**
     * @description function to update the data to database
     * @name updateData
     */        
    async updateData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMEXPENSEITEMSETM,
        {
          data: [
            {
              set_monthly: this.yearMonth,
              industry_no: this.industryNo,
              company_size: this.selectedCompanySize,
              accounts_name_cd: this.accountCd,
              expense_item_cd: this.itemCode,
              sense_of_cost_up: this.upperLimit
                ? this.convertCurrency(this.upperLimit)
                : "",
              sense_of_cost_low: this.lowerLimit
                ? this.convertCurrency(this.lowerLimit)
                : "",
              del_flg: false,
              update_id: localStorage.getItem("client_account_no"),
            },
          ],
          conditions: [
            {
              key: "expense_item_set_y_no",
              value: this.masterData.MonthData.item.set_y_no,
            },
          ],
        }
      );
      if (data.success) {
        this.isBusy = false;
        this.$router.push({ name: "expense_item_add_edit" });
      } else {
        this.isBusy = false;
      }
    },
    /**
     * @description function to create or insert the data  (register to database)
     * @name createData
     */        
    async createData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATEMEXPENSEITEMSETM,
        [
          {
            set_monthly: this.yearMonth,
            industry_no: this.industryNo,
            company_size: this.selectedCompanySize,
            accounts_name_cd: this.accountCd,
            expense_item_cd: this.itemCode,
            sense_of_cost_up: this.upperLimit,
            sense_of_cost_low: this.lowerLimit,
            del_flg: false,
            update_id: localStorage.getItem("client_account_no"),
            create_id: localStorage.getItem("client_account_no"),
          },
        ]
      );
      if (data.success) {
        this.isBusy = false;
        this.$router.push({ name: "expense_item_add_edit" });
      } else {
        this.isBusy = false;
      }
    },
  },
};
</script>

<style>
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
</style>